@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

*{
    font-family:  'Montserrat', sans-serif;
}

.nav{
    position: static;
    display: flex;
    background: #273250;
    justify-content: space-between;
    transition: all ease 0.3s;

}
.nav p {
    font-weight: 800;
    font-size: 32px;
    color: azure;
    align-self: center;
    margin-left: 20px;
    transition: all ease 0.3s;
    cursor: pointer;
}

.nav p:hover {
    color: gold;
}
.nav_list {
    padding: 12px 0;
    display: flex;
}

.nav div  {
    display: flex;
    align-self: center;
}

.nav div img {
    border-radius: 10px;
    border:2px solid white;
}

.nav div h1 {
    font-size: 25px;
    margin-left: 10px;
    margin-top: 9px;
    color: azure;
}


.exit {
    margin-right: 20px;
    font-size: 18px;
    border: none;
    outline: none;
    padding:10px 15px;
    font-weight: 600;   
    border-radius: 10px;
    color: #273250;
    transition: all ease 0.3s;
    cursor: pointer;
    background: white;
}

.exit:hover {
    background: #1b2136 ;
    color: azure;
}
.cont {
    background-color: #1b2136 ;
    width: 100%;
    height: 93.1vh;
    display: flex;
    align-items: center;
    justify-content: center ;
    transition: all ease 0.3s;

}

.chat {
    width: 80%;
    height: 68vh;
    background: rgba(35, 44, 70, 0.7);
    border: 2px solid #273250;
    overflow-y: auto;
}

.mess {
    min-width: 200px;
    margin: 10px;
    padding: 5px;
    width: fit-content;
    border-radius: 10px;
    max-width: 500px;
    max-height: max-content;
    transition: all ease 0.3s;

}

.user_name {
    font-weight: 600;
    font-size: 16px;
    align-self: center;
    margin-left: 10px;
    width: min-content;
}

.user_photo { 
    border:2px solid #1b2136;
    border-radius: 10px;
    
}

.mess_text {
    margin-left: 5px;
    word-wrap: break-word;
    margin-top: 5px;
}
.chat_mess {
    margin-top: 20px;
    width: 80%;
    position: relative;
}
.mess_input {
    
    width: 90%;
    box-sizing: border-box;
    padding: 20px 25px;
    padding-left: 50px;
    border-radius: 10px 0 0 10px;
    border:none;
    font-size: 16px;
    font-weight: 500;
    background: #273250;
    color: azure;
    outline: none;
    transition: all ease 0.3s;
}
.mess_input:focus{
    background:azure;
    color: #273250;
}
.mess_input::placeholder{
    font-size: 18px;
    font-weight: 600;
}

.mess_btn {
    width: 10%;
    padding: 20px 25px;
    font-size: 16px;
    background: azure;
    color: #1b2136;
    font-weight: 700;
    border:none;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
    transition: all ease 0.3s;

}

.mess_input:focus .mess_btn{
    background: #273250;
}


::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: rgba(27, 33, 54, 0.7); 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #0f1320; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }


  .modes {
      width: 42px;
      height: 42px;
      background-size: 32px 32px;
      background-image: url("../img/night-mode.png");
      background-color: white;
      background-repeat: no-repeat;
      background-position: center;
      border: none;
      border-radius: 10px;
      margin-right: 10px;
      transition: all ease 0.3s;
      cursor:pointer;
  }

  .modes:hover {
      transform: scale(1.1);
  }

  .palette {
    position: absolute;
    right: 10px;
    width: 40px;
    display: flex;
    flex-direction: column;
    background: rgba(0, 0, 0, 0.7);
    border-radius: 10px;
    justify-content: center;
    align-items: center;
  }
  .el:nth-child(1){
      margin-top: 8px;
  }
  .el {
    width: 32px;
    height: 32px;
    border-radius: 10px;
    margin-bottom: 8px;
    transition: all ease 0.3s;
      cursor:pointer;
  }
  .el:hover {
      transform: scale(1.1);
  }
  
