* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid #cef;
  border-color: #cef transparent #cef transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}


.loader {
  background: #273250;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}


.lightLoader {
  background: #00b4cc;
}

.redLoader {
  background: #036564;
}

.blueLoader {
  background: #e08e79;

}

.whiteLoader {
  background: #99173c;
}

.purpleLoader {
  background: #99b2b7;
}